}

.fv-swiper.swiper-wrapper.swiper-slide img {
  height: auto;
  width: 100%;
  display: block;
}

.fv-swiper .swiper-img {
  overflow: hidden;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}
.fv-swiper .swiper-slide-active .swiper-img img,
.fv-swiper .swiper-slide-duplicate-active .swiper-img img,
.fv-swiper .swiper-slide-prev .swiper-img img,
.fv-swiper .swiper-img img {
  -webkit-animation: zoomUp 10s linear 0s;
  animation: zoomUp 10s linear 0s; /* 10秒かけて拡大 */
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

/* Swiper-zoom */
@-webkit-keyframes zoomUp {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
  100% {
    -webkit-transform: scale(1.15);
    transform: scale(1.15);
  }
}
@keyframes zoomUp {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
  100% {
    -webkit-transform: scale(1.15);
    transform: scale(1.15);
  }
}
